import {Api_url} from './config'

const Wxcode_url= Api_url+"/auth/wxcode_url"
const Token_url= Api_url+"/auth/gzh_token"

// openidIP  
// ipIP
class WxToken { 	
	constructor() { 
		
	}
	
	verify(e=''){
		let type=''
		if(e=='userinfo'){ 
			type='userinfo'
		}  
		const code = this.GetUrlParame('code') // code  
		var domine = window.location.href.split("#")[0]; // #    # 
		var domine = domine.split("?code")[0]; 
		if (!code) {
			console.log('code')		
			var token = uni.getStorageSync('token'); //
			if (token) { 
				return; 
			}
			uni.request({
			  	url: Wxcode_url,
			  	method: 'GET',  
				data:{
					url:domine,
					type
				},
			  	success: function (res) {   	
					console.log('codes:',res)
					const err=res.data.indexOf('object')//urlobject
					if(res.data && err<0){ 
						window.location.href = res.data;
					}
			  	}
			}); 
		} else {
			console.log('token,:',domine)			
			uni.request({
			  	url: Token_url,
			  	method: 'GET',
				data:{code},
			  	success: function (res) {	
			  		console.log(res.data);
			  		if (res.data.token) { 
			  			uni.setStorageSync("token", res.data.token);						
			  			window.location.href =domine 		
						// uni.switchTab({
						// 	url:'/pages/index/index'
						// })
			  		}
			  	}
			});  
		}
	}
	
	spliceCode(){	
		const code = this.GetUrlParame('code') // code  
		var domine = window.location.href.split("?code")[0]; // #    # 
		if (code) {
			uni.request({
			  	url: Token_url,
			  	method: 'GET',
				data:{code},
			  	success: function (res) {
			  		console.log(res.data);
			  		if (res.data.token) { 
			  			uni.setStorageSync("gzh_token", res.data.token);	
						console.log('end:',res.data.token) 						
			  			window.location.href =domine
			  		}
			  	}
			});  
		}
	}
	
	GetUrlParame(parameName) {  
		/// 
		/// <param name="parameName"></param>
		// url
		var parames = window.location.search
		// 
		if (parames.indexOf(parameName) > -1) {
			var parameValue = ''
			parameValue = parames.substring(parames.indexOf(parameName), parames.length)
			// 
			if (parameValue.indexOf('&') > -1) {
				// ,  parameName=parameValue 
				parameValue = parameValue.substring(0, parameValue.indexOf('&'))
				// , 
				parameValue = parameValue.replace(parameName + '=', '')
				return parameValue
			}
			return ''
		}
	}
} 


export { WxToken };